import React from 'react';
import { Grid, Card, CardActionArea, CardMedia, CardContent, Typography, makeStyles, Theme } from '@material-ui/core';
import { useStaticQuery, graphql } from 'gatsby';

interface QueryResult {
  allMentoringThirdJson: {
    nodes: Array<{
      image: string;
      name: string;
      link: string;
      description: string;
    }>;
  };
}

const useStyles = makeStyles<Theme>(() => ({
  image: {
    height: 200,
  },
}));

const ThirdMentoringProducts = () => {
  const styles = useStyles();
  const {
    allMentoringThirdJson: { nodes: products },
  } = useStaticQuery<QueryResult>(graphql`
    query ThirdMentoringProductsQuery {
      allMentoringThirdJson {
        nodes {
          image
          link
          name
          description
        }
      }
    }
  `);

  return (
    <Grid container spacing={3}>
      {products.map(product => (
        <Grid item xs={12} md={6} key={product.name}>
          <Card>
            <CardActionArea onClick={() => window.open(product.link)}>
              <CardMedia className={styles.image} image={product.image} title={product.name} />
              <CardContent>
                <Typography gutterBottom variant="caption">
                  {product.description}
                </Typography>
                <Typography variant="subtitle1">{product.name}</Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default ThirdMentoringProducts;
