import React, { ReactNode } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core';

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    wrapper: {
      margin: '5rem 0',
      '&::before,&::after': {
        display: 'block',
        content: '"..."',
        textAlign: 'center',
        fontSize: 30,
        color: theme.palette.text.secondary,
        letterSpacing: 4,
      },
      '&::before': {
        marginBottom: 20,
      },
      '&::after': {
        marginTop: 20,
      },
    },
    bollonTitle: {
      fontWeight: 'bold',
      marginBottom: theme.spacing(1),
      '& p': {
        fontWeight: 'bold',
      },
    },
    bollon: {
      border: `1px dashed ${theme.palette.text.secondary}`,
      padding: 12,
      borderRadius: theme.shape.borderRadius,
    },
  })
);

interface Props {
  children: ReactNode;
}
const Conversation = ({ children }: Props) => {
  const styles = useStyles();
  return <div className={styles.wrapper}>{children}</div>;
};

interface BalloonProps {
  person: ReactNode;
  contents: string;
}
const Balloon = ({ person, contents }: BalloonProps) => {
  const styles = useStyles();
  return (
    <dl>
      <dt className={styles.bollonTitle}>{person}</dt>
      <dd className={styles.bollon} dangerouslySetInnerHTML={{ __html: contents }} />
    </dl>
  );
};

Conversation.Balloon = Balloon;

export default Conversation;
