import React, { useMemo } from 'react';
import { Grid } from '@material-ui/core';
import { useAllMembers } from 'src/hooks/members/useAllMembers';
import MemberCard from 'src/components/Cards/MemberCard';
import deepPurple from '@material-ui/core/colors/deepPurple';

interface Props {
  mentors: string[];
}
const Mentors = ({ mentors }: Props) => {
  const members = useAllMembers();
  const pickedMembers = useMemo(() => members.filter(member => mentors.includes(member.id)), [members, mentors]);

  return (
    <Grid container spacing={3}>
      {pickedMembers.map(member => (
        <Grid item xs={12} md={6} key={member.id}>
          <MemberCard member={member} backgroundColor={deepPurple['100']} />
        </Grid>
      ))}
    </Grid>
  );
};

export default Mentors;
